///<reference path="../../../node_modules/@types/node/index.d.ts"/>
import { Component, OnInit, AfterViewInit } from '@angular/core';
import { environment } from '../../environments/environment';
import * as L from 'leaflet';
import 'leaflet-distortable-video';
import { StreamService } from '../stream.service';


@Component({
  selector: 'app-map',
  templateUrl: './map.component.html',
  styleUrls: ['./map.component.css']
})

export class MapComponent implements OnInit {
  map: L.Map;
  defaultZoom = 12;
  defaultCoords: L.LatLng = L.latLng(51.5777, -9.0080);
  mapOptions = {
    minZoom: 1,
    maxZoom: 20,
    id: 'mapbox.satellite',
    accessToken: environment.MAPBOX_API_KEY
  };

  // coords of video corners
  // to be determined by drone coord and alt - getCorner(droneCenter: L.Latlng): L.latlng[]
  corners = {
     topLeft: L.latLng([1, 5]),
     topRight: L.latLng([1, 6]),
     bottomRight: L.latLng([0, 6]),
     bottomLeft: L.latLng([0, 5])
  };
  droneCenter: L.LatLng;
  FOV = 94;   // fov of zenmuse x3 according to DJI website

  // inject WebSocketService to be able to access messages from Kurento Media Sever
  constructor(private stream: StreamService) {
  }

  ngOnInit() {
    // subscribe to socket message observable
    this.stream.messages.subscribe(msg => {
      console.log(msg);
    });

    this.map = L.map('map').setView(this.defaultCoords, this.defaultZoom);
    L.tileLayer('https://api.tiles.mapbox.com/v4/{id}/{z}/{x}/{y}.png?access_token={accessToken}',
     this.mapOptions).addTo(this.map);
  }

  generateVideoLayer(lat1: number, lng1: number, alt: number, hding: number) {
    this.getCorners(lat1, lng1, alt, hding);
    // const waves = L.distortableVideoOverlay('../../assets/video/breaking-waves.mp4', this.corners, {opacity: 0.6}).addTo(this.map);
    const waves = L.distortableVideoOverlay(document.querySelector('video'), this.corners, {opacity: 0.6}).addTo(this.map);
  }

  /* ----------------------------------
   * calculate coords of corners given drone center c, and altitude alt
   * BEFORE rotation => up on screen = north
   ----------------------------------- */
  getCorners(lat1: number, lng1: number, alt: number, hding: number) {
    this.droneCenter = new L.LatLng(Number(lat1), Number(lng1));
    this.map.panTo(this.droneCenter);
    console.log(this.droneCenter.toString());
    const coords = [];  // coords of opposite corners (topRight, bottomLeft)
    const R = this.radiusAtLat(Number(lat1));
    const d = this.distanceFromC(Number(alt));
    // get arctangent of FOV-> constant as FOV has aspect ratio 4:3
    let theta = (Math.atan(4 / 3));


    for (let i = 0; i < 2; i++) {
      // caluclate new Lat
      const temp1 = Math.sin( Number(lat1) * (Math.PI / 180) ) * Math.cos(d / R);
      const temp2 = Math.cos( Number(lat1) * (Math.PI / 180) ) * Math.sin(d / R) * Math.cos(theta);
      const lat2 = (Math.asin( temp1 + temp2) * (180 / Math.PI));
      // calculate new Lng
      const lng2 = Number(lng1) + (Math.atan2 (Math.sin(theta) * Math.sin(d / R) * Math.cos((Number(lat1)) * (Math.PI / 180)),
                   Math.cos(d / R) - Math.sin( Number(lat1) * (Math.PI / 180)) * Math.sin(lat2 * (Math.PI / 180))) * (180 / Math.PI));

      console.log(lat2 + ': lat2,  ' + lng2 + ' :lng2');
      coords.push(new L.LatLng(lat2, lng2));
      theta += (Math.PI);  // add 180deg to theta to get opposite corner
    }
    // test polygon
    L.polygon([ [coords[1].lat, coords[0].lng],
                [coords[1].lat, coords[1].lng],
                [coords[0].lat, coords[1].lng],
                [coords[0].lat, coords[0].lng] ], {color: 'red', opacity: 0.05}).addTo(this.map);

    const coordsToRot = [
      new L.LatLng(coords[1].lat, coords[0].lng),
      new L.LatLng(coords[1].lat, coords[1].lng),
      new L.LatLng(coords[0].lat, coords[1].lng),
      new L.LatLng(coords[0].lat, coords[0].lng)
    ];

    // apply rotation to bounds
    this.rotateCoordAroundCenter(coordsToRot, Number(hding));
  }

  private rotateCoordAroundCenter(coords: L.LatLng[], rot: number): void {
    for (let i = 0; i < coords.length; i++) {
      // translate coordinates to (0,0) to rotate
      const tX = coords[i].lat - this.droneCenter.lat;   const tY = coords[i].lng - this.droneCenter.lng;

      // apply rotation to each coord
      coords[i].lat = (tX * Math.cos(rot * Math.PI / 180) - tY * Math.sin(rot * Math.PI / 180));
      coords[i].lng = (tX * Math.sin(rot * Math.PI / 180) + tY * Math.cos(rot * Math.PI / 180));
      coords[i].lat += this.droneCenter.lat;   coords[i].lng += this.droneCenter.lng;
    }
    this.corners.topLeft = coords[0];
    this.corners.topRight = coords[1];
    this.corners.bottomRight = coords[2];
    this.corners.bottomLeft = coords[3];
  }

  private radiusAtLat(lat: number): number {
    const rEq = 6378137;  // radius of earth at equator (m)
    const rPl = 6356752;  // radius of earth at poles (m)
    const top = this.sqr( this.sqr(rEq) * Math.cos(lat * Math.PI / 180)) + this.sqr( this.sqr(rPl) * Math.sin(lat * Math.PI / 180));
    const bot = this.sqr(rEq * Math.cos(lat * Math.PI / 180)) + this.sqr(rPl * Math.sin(lat * Math.PI / 180));
    return (Math.sqrt(top / bot));
  }

  // calculate distance of corners from center given an altitude alt
  private distanceFromC(alt: number): number {
    return alt * (Math.tan((this.FOV / 2) * Math.PI / 180));
  }

  private sqr(x: number): number {
    return x * x;
  }

}
