import { Injectable } from '@angular/core';
import * as io from 'socket.io-client';
import { Observable } from 'rxjs/Observable';
import * as Rx from 'rxjs/Rx';
import { environment } from '../environments/environment';
import { WebRtcPeer } from '../../node_modules/@types/kurento-utils';


@Injectable({
  providedIn: 'root'
})
export class WebSocketService {

  private socket; // socket that connects to ws server

  constructor() { }

  connect(): Rx.Subject<MessageEvent> {
    // test Websocket for Java Rtp Receiver tutorial
    let ws = new WebSocket('wss://' + location.host + '/player');

    console.log('connect to ws at ' + environment.ws_url);
    this.socket = io(environment.ws_url);


    let observable = new Observable(obs => {
      this.socket.on('message', (data) => {
        console.log('Message received');
        obs.next(data);
      });
      return () => {
        this.socket.disoconnect();
      };
    });

    let observer = {
      next: (data: Object) => {
        this.socket.emit('message', JSON.stringify(data));
      },
    };

    return Rx.Subject.create(observer, observable);
  }
}
